import {app} from 'electron';

const path = require('path');
const crypto = require('crypto');

const ServerConfig: {
    syscoin_config_path: string,
    secret: string,
    port: number | string,
    host: string,
    rpc_host: string
} = {
    syscoin_config_path: path.join(app.getPath('appData'), app.getName(), 'syscoin.conf'),
    secret: crypto.randomBytes(32).toString('hex'),
    port: 8001,
    host: 'localhost',
    rpc_host: 'localhost'
};

const ClientConfig: {
    port: number | string,
    host: string
} = {
    port: 8001,
    host: 'localhost'
};

const BlockmarketConfig: {
    server: object,
    client: object,
} = {
    server: ServerConfig,
    client: ClientConfig
};

export {ServerConfig};
export {ClientConfig};
export default BlockmarketConfig;
